(function() {
  'use strict';

  angular
    .module('chatbox')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('chatbox')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('chatbox')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('chatbox')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;
    vm.messageList = [];
    vm.showDirection = false;
    vm.setShowDirection = setShowDirection;
    vm.directionClick = directionClick;
    vm.showDirectionWork = false;
    vm.directionWorkClick = directionWorkClick;


    $timeout(function() {
      vm.messageList.push({
        message: "Hello, this is Tom.",
        isThey: false
      });
    },1000);
    $timeout(function() {
      vm.messageList.push({
        message: "Welcome to Freestyle",
        isThey: false
      });
    },1200);
    $timeout(function() {
      vm.messageList.push({
        message: "Should I tell more about us or do you want to be",
        isThey: false
      });
      $timeout(vm.setShowDirection,1000);
    },2000);

    function addMessage(message, isThey) {
      vm.messageList.push({
        message: message,
        isThey: isThey
      });
      //Remove first chat item
      vm.messageList.shift();
    }
    
    /**
     * Show direction element
     */
    function setShowDirection() {
      vm.showDirection = true;
    }

    /**
     * Redirect target frame to url
     * @param {*} url 
     */
    function goTo(url) {
      var frame = $("#targetFrame");
      if (frame.length > 0) {
        frame.attr('src', url);
      }
    }

    /**
     * Return to main direction
     */
    function returnToMainDirection() {
      $timeout(function() {
        vm.messageList.push({
          message: "Should I tell more about us or do you want to be",
          isThey: false
        });
        //Remove first chat item
        vm.messageList.shift();
        $timeout(vm.setShowDirection,1000);
      },1000);
    }

    /**
     * Handle click direction to redirect new state
     * @param {any} type type of direction
     */
    function directionClick(type) {
      switch(type) {
        case 'tell':
          //Add message
          vm.messageList.push({
            message: 'Tell me more',
            isThey: true
          });
          //Remove first chat item
          vm.messageList.shift();
          //Hide direction
          vm.showDirection = false;
          //Add next message
          $timeout(function() {
            addMessage("Sure thing!", false);
          },1000);
          $timeout(function() {
            //Add message
            addMessage("Want to see some of our work?", false);
            //Show direction work
            vm.showDirectionWork = true;
          },1500);
          break;
        case 'contact': 
          //Add message
          addMessage("Contact me", true);
          //Hide direction
          vm.showDirection = false;
          // Redirect iframe to work zone
          goTo("http://www.freestyleokc.com/contact/");
          //Show message directed contact
          $timeout(function(){
            addMessage("This is our contact", false);
            //Show direction again
            $timeout(returnToMainDirection,1000);
          },1500);
          break;
      }
    }

    /**
     * Redirect work
     * @param {*} type 
     */
    function directionWorkClick(type) {
      switch (type) {
        case 'film':
          //Add message
          addMessage("Film Production", true);
          //Hide direction
          vm.showDirectionWork = false;
          // Redirect iframe to work zone
          goTo("http://www.freestyleokc.com/work/#index-624697");
          //Show message directed film
          $timeout(function(){
            addMessage("This is our Film production", false);
            //Show direction again
            $timeout(returnToMainDirection,1000);
          },1500);
          break;
        case 'photo':
          //Add message
          addMessage("Photography", true);
          //Hide direction
          vm.showDirectionWork = false;
          // Redirect iframe to work zone
          goTo("http://www.freestyleokc.com/work/#index-624697");

          //Show message directed photo
          $timeout(function(){
            addMessage("This is our Photography", false);
            //Show direction again
            $timeout(returnToMainDirection,1000);
          },1500);
          break;
      }
    }
  }
})();

